



































 	var view
 	export default {
 		data() {
 			return {
 			   siteName:'',
 			   superviseList:[]
 			};
 		},
 		created:function(){
 			view = this;
 		},
 		mounted:function(){
 			this.getList()
 		},
 		watch:{
 		   siteName:function(){
 			   this.getList()
 		   }	
 		},
 		methods:{				
			chosezhandian:function(item){
				console.log(11)
				console.log(item)
				var msg = {
					id:item.id,
					siteName:item.siteName
				}
				var data = JSON.stringify(msg)
				
				uni.setStorageSync('zhandianmsg',data);
				uni.navigateBack({
					delta:1
				});
				  
// 				uni.setStorageSync({
// 					key:'zhandianmsg',
// 					data:JSON.stringify(msg),
// 					success: function () {
// 						
// 					}
// 				});

			},
 			getList:function(){
 				var msg = {	
 					siteName:this.siteName,
 					areaId:'',
 					streetId:'',
 					companyId:'' 
 				}
 				this.$request({
 					url: 'findSiteList',
 					method:'post',
					showLoading:true,
					loadingText:'加载中',
 					data:msg,
 					success:(jsonData) => {
 						console.log(777766)
 						console.log(jsonData)
 						if(jsonData.status){
 							  this.superviseList = jsonData.data
 						}else{
 							uni.showToast({
 								icon:'none',
 								title:jsonData.msg,
 								duration: 1000
 							});
 						}
 					},
 					complete() {
 						view.loading = false;
 					}
 				});
 			},
 		}
 	}
 